<template>
  <!-- 优创二维码充值 -->
  <div
    class="youchuang_qr"
    :class="[logoType == 'wx'? 'youchuang_wxqr' : 'youchuang_zfbqr']"
    v-show="QRshow"
  >
    <header>
      <div></div>
    </header>
    <main>
      <p>￥{{money}}</p>
      <div class="content">
        <qriously :value="QRcode" id="canvas" :size="150"/>
        <!-- <img src="" v-lazy="youchuangQR" alt=""> -->
      </div>
      <h3>{{ faceshow ? '请截图该充值二维码保存相册' : '请用另一部手机扫码支付' }}</h3>
      <p>打开{{logoType == 'wx' ? '微信' : '支付宝'}}</p>
      <p>【扫一扫】</p>
      <p>备注：每次支付必须重新截图新的二维码才能支付成功；一个二维码仅可以支付一次，同个二维码重复支付不到账，谢谢合作。</p>
      <p @click="slideShow = true" v-show="faceshow">如您不懂如何支付，请点击打开支付指引。</p>
    </main>
    <footer>
      <button @click="youchuangMsg">已付款</button>
      <button @click="cancelQRshow">取消</button>
    </footer>
    <div class="youchuangMsg" v-if="youchuangMsgShow">
      <div>
        <h2>充值备注</h2>
        <textarea placeholder="请填写备注" v-model="remark"></textarea>
        <button @click="sureMsg">确 定</button>
      </div>
    </div>
    <slideshow :show="slideShow" :times="7" :logoType="logoType" @carouselShow="carouselShow"></slideshow>
  </div>
</template>
<script>
import slideshow from '../slideshow/index.vue'
export default {
  components: {
    slideshow
  },
  data () {
    return {
      slideShow: false,
      remark: ''
    }
  },
  model: {
    prop: 'QRshow',
    event: 'change'
  },
  props: {
    logoType: {
      type: String,
      default: 'wx'
    },
    order: {
      type: String,
      default: '加载中'
    },
    QRshow: {
      type: Boolean,
      default: false
    },
    faceshow: {
      type: Boolean,
      default: true
    },
    QRcode: {
      type: String,
      default: '加载中'
    },
    youchuangMsgShow: {
      type: Boolean,
      default: false
    },
    money: {
      default: '加载中'
    }
  },
  watch: {
    youchuangMsgShow (val) {
      if (!val) {
        this.remark = ''
      }
    }
  },
  methods: {
    carouselShow () {
      this.slideShow = false
    },
    youchuangMsg () {
      // this.$emit('youchuangMsg')
      this.$emit('update:youchuangMsg', false)
      this.$emit('change', false)
    },
    sureMsg () {
      this.$emit('sureMsg', this.remark)
    },
    cancelQRshow () {
      this.$emit('update:cancelQRshow', false)
      this.$emit('change', false)
      this.remark = ''
    }
  }
}
</script>
<style lang="scss" scope>
@import '@/style/mixin.scss';
.youchuang_qr,
.youchuang_wxqr {
  @include wh(100%);
  @include fb(ver-center);
  @include sr;
  position: fixed;
  top: 0;
  left: 0;
  background: #44cd76;
  z-index: 1000;
  header {
    @include wh(100%, 1.2rem);
    @include fb(center-row);
    background: #ffffff;
    div {
      @include wh(2.5rem, 0.7rem);
      @include bg("~assets/recharge/wxlogo.png");
    }
  }
  main {
    @include space(padding, 0.2rem 0);
    @include space(margin-top, 0.5rem);
    @include fb(center-column);
    width: 94%;
    border-radius: 0.1rem;
    background: #ffffff;
    .content {
      margin: .5rem 0;
      img {
        @include wh(3rem);
      }
    }
    h3 {
      @include sc(0.4rem, #010101);
    }
    p:first-of-type {
      @include sc(0.6rem, #010101);
    }
    p:nth-of-type(2) {
      @include sc(0.5rem, #010101);
    }
    p:nth-of-type(3) {
      padding: 0.2rem 0;
      color: #010101;
    }
    p:nth-of-type(4) {
      width: 96%;
      font-size: 0.3rem;
      color: red;
      padding-top: 0.3rem;
    }
    p:last-of-type {
      padding-top: 0.3rem;
      color: #439ed1;
      text-decoration: underline;
    }
  }
  footer {
    flex: 1;
    @include fb(center-row);
    button {
      @include wh(1.8rem, 0.8rem);
      @include sc(0.3rem, #000000);
      border-radius: 0.1rem;
      background-color: #58bc58;
      &:first-of-type {
        margin-right: 2.6rem;
        color: #ffffff;
      }
      &:last-of-type {
        background: #ffffff;
      }
      &:active {
        opacity: 0.5;
      }
    }
  }
  .youchuangMsg {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.8);
    z-index: 2000;
    > div {
      background: #ffffff;
      border-radius: 0.1rem;
      height: 6rem;
      width: 70%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      h2 {
        padding: 0.3rem;
        font-weight: bold;
      }
      textarea {
        width: 80%;
        height: 3rem;
        margin: 0.2rem 0;
        text-indent: 1em;
      }
      button {
        width: 80%;
        height: 1rem;
        border-radius: 0.1rem;
        margin-top: 0.1rem;
        background-color: #0b9ede;
        color: #ffffff;
        &:active {
          opacity: 0.5;
        }
      }
    }
  }
}
.youchuang_zfbqr {
  background-color: #20759a;
  header {
    div {
        @include bg("~assets/recharge/zfblogo.png");
    }
  }
  footer {
    button:first-child {
      background-color: #0b9ede;
    }
  }
}
</style>
