<template>
  <div class="service">
    <j-header title="客服中心"></j-header>
    <main>
      <nav>请选择QQ客服</nav>
      <a
        v-for="(obj, index) in service"
        :key="index"
        :href="obj.status*1 ? obj.url : 'javascript: void(0)'">
        <span>{{ obj.msg }}</span>
        <span :class="[obj.status*1 ? 'active' : 'default']"></span>
      </a>
    </main>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'

export default {
  name: 'service',
  components: {
    JHeader
  },
  data () {
    return {
      service: []
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      this.request.get('/config').then(res => {
        res.code === 1 && res.data.forEach(obj => {
          if (obj.name === 'custom_qq' || obj.name === 'custom_qq1') {
            this.service.push({
              ...obj,
              url: `http://wpa.qq.com/msgrd?v=3&uin=${obj.value}&site=qq&menu=yes`
            })
          }
        })
      })
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss" scoped>
  @import '@/style/mixin.scss';
  main {
    display: flex;
    flex-direction: column;
    flex: 1;
    nav {
      padding: 0.5rem;
      font-weight: bold;
    }
    a {
      flex: 1;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      @include space(padding, 0 0 0.3rem 0.3rem);
    }
    .default {
      @include wh(1rem);
      @include bg('~assets/home/qq2.png');
    }
    .active {
      @include wh(1rem);
      @include bg('~assets/home/qq.png');
    }
  }
</style>
