<template>
  <div class="jumpPay">
    <j-header title="充 值"></j-header>
    <iframe id="iframeId" :src="url"  frameborder="0" scrolling="auto"></iframe>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'

export default {
  components: {
    JHeader
  },
  data () {
    return {
      url: ''
    }
  },
  created () {
    this.$route.params && (this.url = this.$route.params.url || '')
  }
}
</script>

<style lang="scss" scoped>
  @import '@/style/mixin.scss';
  .jumpPay {
    @include fb(ver);
    iframe{
      flex:1;
      max-width:100%;
      max-height:100%;
      overflow:scroll;
      -webkit-overflow-scrolling: touch;
    }
  }
</style>
