<template>
  <!-- 手动二维码充值 -->
  <div
    class="handle_qr"
    :class="[logoType == 'wx'? 'handle_wxqr' : 'handle_zfbqr']"
    v-if="handleQRshow"
  >
    <header>
      <div></div>
    </header>
    <nav>订单号：{{ order.slice(-3) }}</nav>
    <main>
      <p>￥{{money}}</p>
      <div class="content">
        <img :src="handleQR" alt>
      </div>
      <p>打开{{logoType == 'wx' ? '微信' : '支付宝'}}</p>
      <p>【扫一扫】</p>
      <p>付款时，请输入订单号，否则不能到账</p>
      <p>支付成功后请耐心等候，并与客服保持联系，谢谢合作</p>
      <!-- <p>备注：微信支付时，输入金额下面备注：{{ order.slice(-6) }}，并与客服保持联系，谢谢合作</p> -->
      <!-- <p @click="slideShow = true">如您不懂如何支付，请点击打开支付指引。</p> -->
    </main>
    <footer>
      <p @click="slideShow = true">如您不懂如何支付，请点击打开支付指引。</p>
      <!-- <button @click="handleQRshow = false">充值指引</button> -->
      <div class="button-box">
        <button @click="confirm">付款后提交</button>
        <button @click="cancelQRshow">取消</button>
      </div>
    </footer>
    <div class="handleMsg" v-if="handleMsgShow">
      <div>
        <h2>充值备注</h2>
        <textarea placeholder="请填写备注" v-model="remark"></textarea>
        <button @click="sureMsg">确 定</button>
      </div>
    </div>
    <slideshow :show="slideShow" :logoType='logoType' @carouselShow="carouselShow"></slideshow>
  </div>
</template>
<script>
import slideshow from '../slideshow/index.vue'
export default {
  components: {
    slideshow
  },
  data () {
    return {
      slideShow: false,
      remark: ''
    }
  },
  model: {
    prop: 'handleQRshow',
    event: 'change'
  },
  props: {
    logoType: {
      type: String,
      default: 'wx'
    },
    order: {
      type: String,
      default: '加载中'
    },
    handleQRshow: {
      type: Boolean,
      default: false
    },
    handleQR: {
      type: String,
      default: '加载中'
    },
    handleMsgShow: {
      type: Boolean,
      default: false
    },
    money: {
      type: [String, Number],
      default: '加载中'
    }
  },
  watch: {
    handleMsgShow (val) {
      if (!val) {
        this.remark = ''
      }
    }
  },
  methods: {
    carouselShow () {
      this.slideShow = false
    },
    confirm () {
      this.request({
        url: `payment/${this.order}`,
        method: 'put',
        data: {
          remark: ''
        }
      }).then(res => {
        this.$toast({
          message: res.message,
          position: 'top'
        })
      })
      this.$emit('change', false)
    },
    cancelQRshow () {
      this.$emit('change', false)
    }
  }
}
</script>
<style lang="scss" scope>
@import '@/style/mixin.scss';
.handle_qr,
.handle_wxqr {
  @include wh(100%);
  @include fb(ver);
  @include sr;
  position: fixed;
  top: 0;
  left: 0;
  background: #44cd76;
  z-index: 1000;
  header {
    height: 1.2rem;
    background: #ffffff;
    @include fb(center-row);
    div {
      @include wh(2.5rem, 0.7rem);
      @include bg("~assets/recharge/wxlogo.png");
    }
  }
  nav {
    height: 1rem;
    line-height: 1rem;
    text-align: center;
    color: #ffffff;
    font-size: 0.5rem;
  }
  > main {
    @include space(padding, 0.5rem 0);
    @include fb(center-column);
    width: 94%;
    margin: 0 auto;
    border-radius: 0.1rem;
    background: #ffffff;
    .content {
      margin: 0.5rem 0;
      img {
        @include wh(3rem);
      }
    }
    p:first-of-type {
      @include sc(0.6rem, #010101);
    }
    p:nth-of-type(2) {
      @include sc(0.5rem, #010101);
    }
    p:nth-of-type(3) {
      padding: 0.2rem 0;
      color: #010101;
    }
    p:nth-of-type(4) {
      @include sc(0.3rem, red);
      width: 92%;
      border-top: 1px solid #010101;
      text-align: center;
      font-weight: bold;
      padding-top: 0.3rem;
    }
    p:last-of-type {
      padding-top: 0.3rem;
      width: 92%;
      @include sc(0.26rem, #010101);
    }
  }
  footer {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    p {
      padding: 0.4rem 0;
      transition: all 0.5s ease;
      @include sc(0.26rem, #ffffff);
      text-decoration: underline;
    }
    .button-box {
      display: flex;
      justify-content: center;
      align-items: center;
      button {
        @include wh(1.6rem, .8rem);
        @include sc(0.3rem, #000000);
        border-radius: 0.1rem;
        background-color: #58bc58;
        &:first-of-type {
          margin-right: 2.6rem;
          width: 1.8rem;
          color: #ffffff;
        }
        &:last-of-type {
          background: #ffffff;
        }
        &:active {
          opacity: 0.5;
        }
      }
    }
  }
  .handleMsg {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.8);
    z-index: 2000;
    > div {
      background: #ffffff;
      border-radius: 0.1rem;
      height: 6rem;
      width: 70%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      h2 {
        padding: 0.3rem;
        font-weight: bold;
      }
      textarea {
        width: 80%;
        height: 3rem;
        margin: 0.2rem 0;
        text-indent: 1em;
      }
      button {
        @include wh(80%, 1rem);
        border-radius: 0.1rem;
        margin-top: 0.1rem;
        background-color: #0b9ede;
        color: #ffffff;
        &:active {
          opacity: 0.5;
        }
      }
    }
  }
}
.handle_zfbqr {
  background-color: #20759a;
  header {
    div {
        @include bg("~assets/recharge/zfblogo.png");
    }
  }
  footer {
    .button-box button {
      background-color: #0b9ede;
    }
  }
}
</style>
