<template>
  <div class="block" v-if="show">
    <button class="goback" @click="carouselBtn">返回</button>
    <j-swiper
        :list="list"
        height="100%"
        :interval="3000"
        :duration="366"
        :auto="true">
    </j-swiper>
  </div>
</template>
<script>
import JSwiper from '@/components/JSwiper'

export default {
  components: {
    JSwiper
  },
  data () {
    return {
      list: []
    }
  },
  props: {
    show: {
      type: Boolean,
      default: true
    },
    times: {
      type: Number,
      default: 8
    },
    logoType: String
  },
  created () {
    let array = []
    for (let i = 1; i < 9; i++) {
      array.push({
        url: 'javascript:',
        img: `${this.$baseUrl}img/t_imgs/pay/guide${i}${this.logoType}.png`
      })
    }
    this.list = array
  },
  methods: {
    carouselBtn () {
      this.$emit('carouselShow')
    }
  }
}
</script>
<style lang="scss" scope>
.block {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  .goback {
    position: absolute;
    top: 0.2rem;
    left: 0.2rem;
    height: 1rem;
    width: 1rem;
    color: #ffffff;
    z-index: 2222;
    background: #2366f5;
    border-radius: 50%;
  }
}
</style>
