<template>
  <div class="recharge">
    <j-header title="充 值"></j-header>
    <main>
      <h2>
        <svg-icon icon-class="money"></svg-icon>
        <span>充值金额</span>
      </h2>
      <ul class="money-list">
        <li class="wave" v-for="obj in moneyData" :key='obj.id' @click="changeMoney($event, obj)">
          <span :class="[obj.is_default === 10 ? 'money-active' : '']">￥{{ obj.value }}</span>
        </li>
      </ul>
      <h2>
        <svg-icon icon-class="rechargetype"></svg-icon>
        <span>支付方式</span>
      </h2>
      <ul class="type-list">
        <li v-waves v-for="obj in typeData" :key='obj.id' @click="changeType($event, obj)">
          <p>
            <img :src="obj.img" alt="">
            <span>{{ obj.name }}</span>
          </p>
          <span :class="[obj.is_default === 10 ? 'type-active' : '']"><i></i></span>
        </li>
      </ul>
      <div class="button-box">
        <button @click="submit">立即支付</button>
      </div>
    </main>
    <!-- 表单提交 -->
    <div ref="form" v-html="form" v-show="false"></div>
    <!-- 手工充值 -->
    <handle-recharge
      v-model="handle.handleshow"
      :money="handle.randomAmount"
      :order='handle.order'
      :logoType='logoType'
      :handleQR='handle.handleQR'>
    </handle-recharge>
    <!-- 云闪付 -->
    <ysf-recharge
      v-model="YSF.show"
      :qrcode="YSF.qrcode">
    </ysf-recharge>
    <!-- 优创 -->
    <you-chuang
        v-model="youChuang.show"
        :money="money"
        :QRcode="youChuang.QRcode"
        :faceshow="youChuang.faceshow"
        :logoType="logoType">
    </you-chuang>
    <!-- 跳转微信支付 -->
    <jump-wechat
      v-model="jumpWechat.show"
      :money="money"
      :url="jumpWechat.url">
    </jump-wechat>
  </div>
</template>

<script>
// @ is an alias to /src
import { judgeSystem } from '@/utils'
import JHeader from '@/components/JHeader'
import handleRecharge from './components/handleRecharge'
import ysfRecharge from './components/ysfRecharge'
import youChuang from './components/youChuang'
import jumpWechat from './components/jumpWechat'

export default {
  name: 'recharge',
  components: {
    JHeader,
    handleRecharge,
    ysfRecharge,
    youChuang,
    jumpWechat
  },
  data () {
    return {
      moneyData: [],
      typeData: [],
      handleData: [],
      logoType: 'wx',
      money: '',
      type: '',
      form: '',
      status: '', // 区分手动还是自动
      // 手工充值参数
      handle: {
        handleshow: false,
        order: '',
        handleQR: require('@/assets/recharge/wxlogo.png'), // 手工充值二维码
        randomAmount: '' // 手工充值随机金额
      },
      // 银闪付参数
      YSF: {
        show: false,
        qrcode: ''
      },
      // 优创参数
      youChuang: {
        show: false,
        QRcode: '',
        faceshow: true
      },
      // 跳转微信支付参数
      jumpWechat: {
        show: false,
        url: ''
      }
    }
  },
  created () {
    this.init()
  },
  methods: {
    async init () {
      await this.request.get('/config').then(res => {
        res.code === 1 && res.data.forEach(o => {
          if (o.name === 'weixin_qr_1_off' && o.value * 1 === 10) {
            this.handleData.push({
              img: require('@/assets/recharge/wx.png'),
              is_default: 1,
              name: '微信收款码1(人工充值)',
              status: 30, // 充值类型：10：支付, 30: 转账
              type: 101
            })
          } else if (o.name === 'weixin_qr_2_off' && o.value * 1 === 10) {
            this.handleData.push({
              img: require('@/assets/recharge/wx.png'),
              is_default: 1,
              name: '微信收款码2(人工充值)',
              status: 30, // 充值类型：10：支付, 30: 转账
              type: 102
            })
          } else if (o.name === 'zhifubao_qr_1_off' && o.value * 1 === 10) {
            this.handleData.push({
              img: require('@/assets/recharge/zfb.png'),
              is_default: 1,
              name: '支付宝收款码1(人工充值)',
              status: 30, // 充值类型：10：支付, 30: 转账
              type: 103
            })
          }
        })
      })
      await this.request.get('/pay-config').then(res => {
        res.code === 1 && (this.moneyData = res.data) && res.data.forEach(o => {
          o.is_default === 10 && (this.money = o.value)
        })
      })
      await this.request.get('/payment').then(res => {
        res.code === 1 && res.data.forEach(obj => {
          obj.status = 10
          if (obj.is_default === 10) {
            this.type = obj.id
            this.status = obj.status
          }
        })
        res.code === 1 && (this.typeData = res.data.concat(this.handleData))
      })
    },
    changeMoney (e, obj) {
      this.moneyData.forEach(o => {
        o.is_default = 1
      })
      obj.is_default = 10
      this.money = obj.value
    },
    changeType (e, obj) {
      this.typeData.forEach(o => {
        o.is_default = 1
      })
      obj.is_default = 10
      this.status = obj.status
      obj.type && (this.type = obj.type)
      !obj.type && (this.type = obj.id)
    },
    randomMoney () {
      this.handle.randomAmount = this.status === 30 ? this.money * 1 + Math.random().toFixed(2) * 1 : this.money
      return this.handle.randomAmount
    },
    submit () {
      this.request({
        url: '/payment',
        method: 'post',
        data: {
          amount: this.randomMoney(),
          type: this.type,
          state: this.status
        },
        loading: true
      }).then(res => {
        if (res.code === 1) {
          if (this.status === 30) {
            if (this.type === 101 || this.type === 102) {
              this.logoType = 'wx'
            } else if (this.type === 103) {
              this.logoType = 'zfb'
            }
            this.handle.order = res.data.order_no
            this.handle.handleQR = res.data.qrcode_url
            this.handle.handleshow = true
          } else if (this.status === 10) {
            if (res.data.state === 10) {
              if (judgeSystem()) {
                this.$router.push({
                  name: 'jumpPay',
                  params: { url: res.data.pay_url }
                })
              } else {
                window.location.href = res.data.pay_url
              }
            } else if (res.data.state === 20) {
              // 优创
              if (
                res.data.pay_url.indexOf('weixin://') !== -1 ||
                res.data.pay_url.indexOf('http://uchannel') !== -1
              ) {
                if (res.data.pay_url.indexOf('weixin://') !== -1) {
                  this.youChuang.faceshow = false
                } else if (
                  res.data.pay_url.indexOf('http://uchannel') !== -1
                ) {
                  this.youChuang.faceshow = true
                }
              }
              this.logoType =
                res.data.pay_url.indexOf('https://qr.alipay') !== -1
                  ? 'zfb'
                  : 'wx'
              this.youChuang.show = true
              this.youChuang.QRcode = res.data.pay_url
              // 其他
              // this.qrcode = res.data.pay_url
              // this.QRshow = true
            } else if (res.data.state === 30) {
              // 表单提交
              this.form = res.data.from
              setTimeout(() => {
                document.querySelector('form').submit()
              }, 1000)
              // document.querySelector('body').append(res.data.from)
            } else if (res.data.state === 40) {
              let ua = navigator.userAgent.toLowerCase()
              let isWeixin = ua.indexOf('micromessenger') !== -1
              if (isWeixin) {
                document.querySelector('body').append(res.data.from)
              } else {
                // 跳转微信支付
                this.jumpWechat.url = res.data.pay_url
                this.jumpWechat.show = true
              }
            } else if (res.data.state === 50) {
              // 云闪付
              this.YSF.qrcode = res.data.pay_url
              this.YSF.show = true
            }
          }
        } else {
          this.$toast(res.message)
        }
      })
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
