<template>
  <!-- 手动二维码充值 -->
  <div class="jun-jump-page" v-show="show">
    <j-header title="充 值" :go="false" @goback="$emit('change', false)" bg="#3baf36" icon="" icon-router=""></j-header>
    <div class="jun-paypage-box">
      <h3>请确认充值金额</h3>
      <div class="jun-money-box">
        <h1>
          ￥
          <span>{{money}}</span>
        </h1>
        <div class="jun-info-box">
          <p>温馨提示：</p>
          <div>
            <p>1.请确定充值金额,若不确定充值请点击取消返回,进入付款页面请及时付款</p>
            <p>2.如充值未到账情况，请及时联系客服！</p>
            <input type="text" v-model="url" ref="copyUrl" style="opacity:0;height:1px;width:1px;">
          </div>
        </div>
      </div>
      <div class="jun-otherpay-btn">
        <button @click="jumpWX" v-waves>确认支付</button>
        <button @click="hide" v-waves>取消支付</button>
      </div>
      <div class="jun-bg-img"></div>
    </div>
  </div>
</template>
<script>
import JHeader from '@/components/JHeader'
import { copy, jumpWechat } from '@/utils'

export default {
  components: {
    JHeader
  },
  data () {
    return {
      copyUrl: ''
    }
  },
  model: {
    prop: 'show',
    event: 'change'
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    url: String,
    money: {
      type: [String, Number],
      default: '加载中'
    }
  },
  methods: {
    jumpWX () {
      copy(this.$refs.copyUrl)
      this.$confirm('已复制充值链接，请打开微信聊天页面粘贴链接，并点击链接进行充值操作。').then(() => {
        // 跳转微信
        jumpWechat()
      }).catch(() => {
        jumpWechat()
      })
    },
    hide () {
      this.$emit('change', false)
    }
  }
}
</script>
<style lang="scss" scope>
@import "@/style/mixin.scss";
.jun-jump-page {
  position: fixed;
  top: 0;
  left: 0;
  @include wh(100%);
  @include fb(ver-center);
  background-color: #fff;
  z-index: 2000;
  .jun-paypage-box {
    flex: 1;
    width: 100%;
    @include sr;
    @include fb(ver-center);
  }
  h3 {
    @include wh(96%, .8rem);
    @include space(margin-top, 0.3rem);
    @include space(padding-bottom, 0.2rem);
    @include sc(0.33rem, #000000);
    border-bottom: 1px solid #999999;
  }
  .jun-money-box {
    min-height: 6rem;
    width: 100%;
    background: #f5faff;
    margin-top: 0.5rem;
    display: flex;
    flex-direction: column;
    // justify-content: center;
    align-items: center;
    h1 {
      width: 80%;
      height: 1rem;
      line-height: 1rem;
      text-align: center;
      margin-top: 0.5rem;
      border-bottom: 1px solid #999;
      @include sc(0.6rem, #000000, bold);
      span {
        @include sc(0.5rem, #fe0000, bold);
      }
    }
    .jun-info-box {
      width: 90%;
      margin: 0.4rem 0;
      div {
        border: 1px dashed #999;
        border-radius: 0.2rem;
        padding: 0.2rem 0;
        margin-top: 0.4rem;
        font-size: 0.4rem;
        min-height: 1rem;
        @include fb(ver-center);
        p {
          width: 90%;
          @include sc(0.3rem, #666666);
          padding: 0.06rem 0;
        }
      }
    }
  }
  .jun-otherpay-btn {
    width: 100%;
    margin-top: .6rem;
    @include fb(ver-center);
    button {
      width: 88%;
      height: 1rem;
      border-radius: 0.1rem;
    }
    button:nth-child(1) {
      background: #3baf36;
      color: #ffffff;
    }
    button:nth-child(2) {
      color: #666;
      background-color: #dddddd;
      margin-top: 0.6rem;
    }
  }
  .jun-bg-img {
    height: 2rem;
    width: 60%;
    margin-top: .5rem;
    @include bg('~assets/recharge/wechat.png');
  }
}
</style>
